import { useSignal } from '@viewfly/core'

const infos = [
    `React, propschildren`,
    `, props, `
]
const child1Title = `child1props, `
const child2Title = `child2props, `
const child3Title = `child3props, `
const child4Title = `child4props, `

const codeSnippets = [
    `function Child1(props) {
        return () => {
            console.log('child1 render', props, props.age);
            return <>
                <p>{child1Title}</p>
                <span>name: {props.name} age: {props.age}</span>
                <hr />
            </>
        }
    }`,
    `function Child2(props) {
        return () => {
            const { name, age } = props;
            console.log('child2 render', props, props.age);
            return <>
                <p>{child2Title}</p>
                <span>name: {name} age: {age}</span>
                <hr />
            </>
        }
    }`,
    `function Child3(props) {
        const { name, age } = props;
        return () => {
            console.log('child3 render', props, props.age);
            return <>
                <p>{child3Title}</p>
                <span>name: {name} age: {age}</span>
                <hr />
            </>
        }
    }`,
    `function Child4({ name, age }) {
        return () => {
            console.log('child4 render', age);
            return <>
                <p>{child4Title}</p>
                <span>name: {name} age: {age}</span>
            </>
        }
    }`,
]

// props, age
function Child1(props) {
    return () => {
        console.log('child1 render', props, props.age);
        return <>
            <p>{child1Title}</p>
            <span>name: {props.name} age: {props.age}</span>
            <hr />
        </>
    }
}
// propsage
function Child2(props) {
    return () => {
        const { name, age } = props;
        console.log('child2 render', props, props.age);
        return <>
            <p>{child2Title}</p>
            <span>name: {name} age: {age}</span>
            <hr />
        </>
    }
}

// propsage
function Child3(props) {
    const { name, age } = props;
    return () => {
        console.log('child3 render', props, props.age);
        return <>
            <p>{child3Title}</p>
            <span>name: {name} age: {age}</span>
            <hr />
        </>
    }
}
// props, age
function Child4({ name, age }) {
    return () => {
        console.log('child4 render', age);
        return <>
            <p>{child4Title}</p>
            <span>name: {name} age: {age}</span>
        </>
    }
}

export default function Component() {
    const user = useSignal({
        name: 'jim',
        age: 20,
    })
    const update = () => {
        const tmp = user();
        user.set({
            ...tmp,
            age: tmp.age + 1,
        })
    }
    return () => {
        return (
            <>
                <ul style="padding-left: 0.2rem;">
                    {
                        infos.map(item => <li>{item}</li>)
                    }
                </ul>
                <button style="margin: 6px;" onClick={update}>
                    age++
                </button>
                <Child1 {...user()}></Child1>
                <Child2 {...user()}></Child2>
                <Child3 {...user()}></Child3>
                <Child4 {...user()}></Child4>
                <div class="code-snippets">
                    {
                        codeSnippets.map(item => <pre><code>{item}</code></pre>)
                    }
                </div>
            </>
        )
    }
}